import React from "react";
const Text = (props) =>{
    const {rules, action} = props;
    return (<div>
       <p className="text-light"> Kathrine soon woke, still tired but her mood quickly brightened when she saw the pen next to her. A smile spread from ear to ear as she quickly got dressed. The school uniform was the usual stuff. A shirt, skirt, stockings, bra and panties. She looked over the bra. The little label on it proudly proclaimed it to be a <span onClick={()=>action({bigBreasts:!rules.bigBreasts})} className="text-weight-bold text-danger Hand">{rules.bigBreasts ? "DD-cup":"B-cup"}</span>. Sarah put on her outfit and hurried downstairs, finding her Mother already in the kitchen making breakfast.</p>
<br/>
<img src={require("../../resources/Mom.jpg")} alt="Kathrine's Mom" className="rounded img-fluid"/>
<br/>
<p className="text-light">She was walking around in her favourite lingerie as per usual, which hardly bothered Kathrine at this point. Her Mother had always been a bit of a slag. The newspaper had been opened next to the plate with food. The headline proudly proclaiming:</p>
<div className="bg-secondary"><h5 className="text-light text-center text-weight-bold">New study finds libido in women <span onClick={()=>action({libidoIncreased:!rules.libidoIncreased,skema:[
    {...rules.skema[0], class: "Sexual control",
    classChange: false,
    teacher: "Miss Peach",
    teacherChange: true,
    },
    {...rules.skema[1], class: "Physical Exercise",
    classChange: false,
    teacher: "Madam Green",
    teacherChange: true,
    },
    {...rules.skema[2], class: "Sexual Etiquette",
    classChange: false,
    teacher: "Miss Janny",
    teacherChange: true
    },
    {...rules.skema[3],class: "Science",
    classChange: false,
    teacher: "Professor Rack",
    teacherChange: true
    },
]})} className="text-weight-bold text-danger Hand">{rules.libidoIncreased ? "ten times greater":"equal"}</span> to that of men</h5></div>
<p className="text-light">At the bottom of the page were small notes with who had been married in the local area. <span className="Hand text-danger text-weight-bold" onClick={()=>action({incest:!rules.incest})}>{rules.incest ? "Her own name, paired with that of her mother's,":"A couple of names"}</span> sprang to mind when reading the page.</p>

    </div>)
}
export default Text;