const initState = {
    start:"Like this",
    power: true,
    bool: false,
    hungry:false,
    skema:[
        {class:"Maths",classChange:false,teacher:"Miss Peach",teacherChange:true},
        {class:"Physical Exercise",classChange:false,teacher:"Mister jern",teacherChange:false},
        {class:"Primary  language",classChange:false,teacher:"Mr. Woods",teacherChange:false},
        {class:"Science",classChange:false,teacher:"Professor Rack",teacherChange:true}
    ],
    quizChoice:null,
    bigBreasts:false,
    libidoIncreased:false,
    bimbo: false,
    allGirls: false,
    incest: false,
    foodChoice: null,
    transformationMachine: false,
    preparedToken:0,
}

export const choiceReducer = (state = initState, action) =>{
    switch(action.type){
        case "CHANGE_ITEMS":
            return {...state,...action.payload}
        case "RESET_ITEMS":
            return initState
        default:
            return state
    }
}